/*colores*/
/*Tama単os de letras*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Titillium Web", sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  padding: 0;
}

/*Tama単o que tendra nuestra pagina para que no crezca*/
.contenido-max {
  max-width: 110rem;
  width: 90%;
  margin: 0 auto;
}

.contenido-dos {
  max-width: 80rem;
  width: 80%;
  margin: 0 auto;
}

.contenedor {
  max-width: 110rem;
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
}

/*Are de la navegaciomn*/
.barra-navegacion {
  background-color: #2E2E2E;
}

.imagen-icono > img {
  width: 8rem;
  border-radius: 50%;
}

.legacy {
  margin: 0;
  padding: 0;
  color: #e7e1e1;
}

.enlaces {
  font-size: 1.5rem;
  margin-right: 1.5rem;
  color: #e7e1e1;
  text-transform: uppercase;
}

.enlaces {
  position: relative;
  padding-bottom: 1rem;
  transition: all 0.3s;
}

/*Efectos a los enlaces*/
.enlaces::before {
  content: "";
  display: block;
  margin: 0;
  width: 0;
  height: 5px;
  background: linear-gradient(to right, #4C0B5F, #e7e1e1);
  position: absolute;
  bottom: 0;
  transition: all 0.3s;
}

.enlaces:hover {
  color: var(--oscuro-opaco);
}

.enlaces:hover:before {
  width: 100%;
}

.enlaces:before {
  left: 50%;
}

.enlaces:hover:before {
  left: 0;
}

.icono-hamburguesa img {
  width: 5rem;
  filter: invert(100%);
}

/*Navegacion mobile*/
.navegacion-mobile {
  position: fixed;
  top: 0;
  left: 0;
  right: auto;
  height: 100vh;
  background-color: #615e5e;
  padding: 2rem;
  box-shadow: -10px 0 40px #2E2E2E;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in;
}

@media (min-width: 350px) {
  .navegacion-tab-desk {
    display: none;
  }
}
@media (min-width: 768px) {
  .navegacion-tab-desk {
    display: block;
  }

  .icono-hamburguesa {
    display: none;
  }

  .navegacion-mobile {
    display: none;
  }
}
.show-nav-mobile {
  visibility: visible;
  opacity: 1;
  transition: 0.3s ease-in;
}

.nav-mobile {
  display: flex;
  flex-direction: column;
}

.nav-mobile a {
  margin-bottom: 2rem;
  text-align: center;
  margin-right: 0;
  padding: 2rem;
}

.nav-mobile a:hover {
  color: #2E2E2E;
}

.contenido-max {
  max-width: 110rem;
  width: 90%;
  margin: 0 auto;
}

.proyectos img {
  width: 30rem;
}

.icon-dark {
  position: absolute;
  top: 20px;
}

.presentacion {
  margin-top: 7rem;
}
.presentacion .name {
  text-align: center;
  font-size: 4rem;
  color: #615e5e;
}

.description {
  display: flex;
  flex-direction: column;
}
.description .myPhoto {
  margin: 0 auto;
}
.description .info-my p {
  font-size: 2rem;
  line-height: 2.5rem;
  text-align: center;
}

@media (min-width: 768px) {
  .description {
    flex-direction: row;
    gap: 3rem;
    margin-top: 6rem;
  }
  .description .myPhoto img {
    width: 40rem;
    height: 40rem;
  }
  .description .info-my p {
    text-align: initial;
  }
}
#habilidades {
  background-color: #d3c3c3;
  margin-top: 8rem;
  border-radius: 0.5rem;
  padding: 4rem;
}
#habilidades h2 {
  text-align: center;
  font-size: 4rem;
  margin-top: 0;
  color: #615e5e;
}
#habilidades .descripcion-habilidades {
  max-width: 60rem;
  margin: 0 auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
#habilidades .descripcion-habilidades div {
  background-color: #615e5e;
  border-radius: 0.5rem;
  padding: 2rem;
}
#habilidades .descripcion-habilidades div .icons {
  display: flex;
  justify-content: center;
  align-items: center;
}
#habilidades .descripcion-habilidades div .icons img {
  width: 5rem;
}
#habilidades .descripcion-habilidades div h3 {
  text-align: center;
  font-size: 2rem;
  color: #e7e1e1;
  margin-top: 0;
}
@media (min-width: 768px) {
  #habilidades .descripcion-habilidades {
    grid-template-columns: repeat(3, 1fr);
  }
}